body {
    font-family: Arial, sans-serif;
    background: linear-gradient(to bottom right, #e1e1e1, #4fc5f3);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
  }
  
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(to bottom right, #e1e1e1, #d19cdf);
  }
  
  .card {
    background: linear-gradient(145deg, #ebece2, #a9d1ec);
    border-radius: 10px;
    box-shadow:  5px 5px 15px #a3a3a3,
                 -5px -5px 15px #ffffff;
    padding: 20px;
    width: 90%;
    max-width: 500px;
    text-align: center;
  }
  
  .recording-indicator {
    margin-top: 10px;
    font-weight: bold;
    color: red;
    font-size: 18px;
  }
  
  
  h1 {
    margin-bottom: 20px;
  }
  
  label {
    margin-right: 10px;
  }
  
  select {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  .buttons, .action-buttons {
    margin: 20px 0;
  }
  
  button {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    margin: 5px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .transcript-box {
    background: #f9f9f9;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    min-height: 50px;
    margin: 10px 0;
  }
  
  h2 {
    margin: 0;
  }
  